import { types } from "mobx-state-tree";
import { Board } from "./GameBoard";

export const GameState = types
  .model("GameState", {
    move_count: types.number,
    state: Board,
    grid_size: types.number,
    game_code: types.string,
    win_state: types.union(types.boolean, types.string),
    player1: types.union(types.string, types.null),
    player2: types.union(types.string, types.null),
    emblem1: types.string,
    emblem2: types.string
  })
  .actions(self => ({
    markWon() {
      self.win_state = true;
    },
    setMoves(moves) {
      self.move_count = moves;
    },
    setGameState(state) {
      //self.state.rows[x].cells[y].state = state;
      self.state = state;
    }
  }))
  .views(self => ({
    isWon() {
      return self.win_state ? "Winning!" : "Make a move";
    },
    whichEmblem() {
      return self.move_count % 2 === 0 ? self.emblem1 : self.emblem2;
    }
  }));



