import React from "react";
import ReactDOM from "react-dom";
import App from "./components/App";
import registerServiceWorker from "./registerServiceWorker";
import { GameState } from "./models/GameState";
import makeInspectable from "mobx-devtools-mst";
import socket from "./socket";

const gameCode = window.location.pathname.slice(1);

socket.on("init_game_state", state => {
  const initGameState = JSON.parse(state);

  const gameState = GameState.create(initGameState);

  console.log("init_game_state");

  makeInspectable(gameState);

  ReactDOM.render(
    <App gameState={gameState} />,
    document.getElementById("root")
  );
  registerServiceWorker();
});

socket.emit("enter_room", gameCode);

socket.emit("get_game_state", gameCode);



