import { types, getParent } from "mobx-state-tree";
import socket from "../socket";

const Cell = types
  .model("Cell", {
    state: types.string
  })
  .actions(self => ({
    make_move(x, y) {
      if (!getParent(self, 5).win_state) {
        const emblem = getParent(self, 5).whichEmblem();
        socket.emit("make_move", self.getGameCode(), { x: x, y: y }, emblem);
      }
    }
  }))
  .views(self => ({
    getGameCode() {
      return getParent(self, 5).game_code;
    }
  }));

const Row = types.model("Row", {
  cells: types.array(Cell)
});

export const Board = types.model("Board", {
  rows: types.array(Row)
});



