import React, { Component } from "react";
import { observer } from "mobx-react";
import Board from "./Board";
import { Style } from "radium";
import socket from "../socket";

class App extends Component {
  constructor(props) {
    super(props);
    socket.on("game_state_change", state_json => {
      const state = JSON.parse(state_json);
      this.props.gameState.setGameState(state.state);
      this.props.gameState.setMoves(state.move_count);
      state.win_state ? this.props.gameState.markWon() : {};
    });
  }

  getTableStyle() {
    const { grid_size } = this.props.gameState;
    const styles = {
      table: {
        margin: "1px auto",
        borderCollapse: "collapse"
      },
      td: {
        width: "100px",
        height: "100px",
        border: "6px solid #222",
        fontSize: "300%",
        textAlign: "center",
        verticalAlign: "middle"
      },
      "td:first-of-type": {
        "border-left-color": "transparent"
      },

      "tr:nth-of-type(1) td": {
        "border-top-color": "transparent"
      },

      [`td:nth-of-type(${grid_size})`]: {
        "border-right-color": "transparent"
      },

      [`tr:nth-of-type(${grid_size}) td`]: {
        "border-bottom-color": "transparent"
      }
    };
    return styles;
  }

  render() {
    const { gameState } = this.props;
    return (
      <div className="App">
        <h1>{gameState.game_code}</h1>
        <Style rules={this.getTableStyle()} />
        <div className="board">
          <Board state={gameState.state} />
        </div>
        {gameState.move_count} - {gameState.isWon()}
      </div>
    );
  }
}

export default observer(App);



