import React, { Component } from "react";
import Cell from "./Cell";

class Row extends Component {
  render() {
    const { row, rowIndex } = this.props;

    const cells = row.cells.map((item, index) => {
      return (
        <Cell cell={item} key={index} rowIndex={rowIndex} cellIndex={index} />
      );
    });

    return <tr id={rowIndex}>{cells}</tr>;
  }
}

export default Row;



