import React, { Component } from "react";
import Row from "./Row";

class Board extends Component {
  render() {
    const { state } = this.props;
    const rows = state.rows.map((item, index) => {
      return <Row row={item} key={index} rowIndex={index} />;
    });

    return (
      <div>
        <table>
          <tbody>{rows}</tbody>
        </table>
      </div>
    );
  }
}

export default Board;



